<script>


export default {
  components: {  },
  data: () => {

    return {
      monthNames: [
        'January',
        'February',
        'Março',
        'Abril',
        'Maio',
        'Junho',
        'Julho',
        'Agostu',
        'Setembro',
        'Outubro',
        'Novembro',
        'Dezembro'
      ],
      dayNames: [
        'Domingo',
        'Segunda',
        'Terça',
        'Quarta',
        'Quinta',
        'Sexta',
        'Sábado'
      ]
    };

  },

  computed: {
    today() {
      return new Date()
    },
    year() {
      return this.today.getFullYear()
    },
    month() {
      return this.today.getMonth()
    },
    dayOfTheMonth() {
      return this.today.getDate()
    },
    dayOfTheWeek() {
      return this.today.getDay()
    },
    firstDay() {
      return new Date(this.year, this.month, 1)
    },
    lastDay() {
      return new Date(this.year, this.month + 1, 0)
    },
    firstDayOfTheMonth() {
      return this.firstDay.getDate()
    },
    firstDayOfTheWeek() {
      return this.firstDay.getDay()
    },
    lastDayOfTheMonth() {
      return this.lastDay.getDate()
    },
    lastDayOfTheWeek() {
      return this.lastDay.getDay()
    }
  },
  methods: {
    getMonthName(index) {
      return this.monthNames[index]
    }
  }

};
</script>

<template>
  <div id="root" class="text-center h-screen flex flex-col">
    <h1 class="py-2 font-bold">{{ getMonthName(month) }}, {{ year }}</h1>

    <ol class="grid grid-cols-7 gap-1">
      <li v-for="dayName in dayNames" :key="dayName" class="truncate p-1 border border-black">
        {{ dayName }}
      </li>
    </ol>

    <ol class="grid grid-cols-7 flex-grow gap-1 pt-1">
      <li
          v-for="dayMonth in lastDayOfTheMonth"
          :key="dayMonth"
          class="p-1 flex justify-center items-center border border-black"
          :class="[
        `${dayMonth === 1 ? `col-start-${firstDayOfTheWeek + 1}` : ''}`,
        `${dayMonth === dayOfTheMonth ? 'bg-blue-500 text-white' : ''}`
      ]">
        {{ dayMonth }}
      </li>
    </ol>
  </div>
</template>